@import "https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap";
html {
  --link-color: #00acdc;
  margin: 0;
  font-family: Roboto, sans-serif;
}

body {
  background-color: gray;
  margin: 0;
}

.header {
  box-sizing: border-box;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.img {
  height: 40px;
}

.ul {
  margin: 0;
  padding: 0;
  display: flex;
}

.li {
  margin-right: 20px;
  list-style: none;
  position: relative;
}

.last-li {
  margin-right: 0;
}

.nav__links {
  color: #000;
  text-transform: uppercase;
  white-space: nowrap;
  align-items: center;
  height: 60px;
  padding: 0;
  font-size: 12px;
  text-decoration: none;
  display: flex;
}

.nav__links:hover {
  color: var(--link-color);
}

.is-active:after {
  content: "";
  background-color: var(--link-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.is-active {
  color: var(--link-color);
}
